<!--********** surname **********-->
			<div class="item">
				<label for="surname" class="required">–§–∞–º–∏–ª–∏—è:</label>
				<input type="text" id="surname" name="surname"
					pattern="[–ê-–Ø–∞-—è—ë–Å]{3,}"
					required
					v-model.trim="surname"
					@input="setSurname($event.target.value)"
				/>
				<span class="error-title" v-if="$v.surname.$dirty && !$v.surname.minLength">–ü–æ–ª–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º {{$v.surname.$params.minLength.min}} –±—É–∫–≤—ã</span>
				<span class="error" v-if="($v.surname.$dirty && !$v.surname.minLength) || !$v.surname.required" >&#10094;</span>
				<span class="done" v-if="$v.surname.$dirty && $v.surname.required && $v.surname.minLength">&#10003;</span>
			</div>
<!--********** name **********-->
			<div class="item">
				<label for="name" class="required">–ò–º—è:</label>
				<input type="text" id="name" name="name"
					pattern="[–ê-–Ø–∞-—è—ë–Å]{3,}"
					required
					v-model.trim="name"
					@input="setName($event.target.value)"
				/>
				<span class="error-title" v-if="$v.name.$dirty && !$v.name.minLength">–ü–æ–ª–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º {{$v.name.$params.minLength.min}} –±—É–∫–≤—ã</span>
				<span class="error" v-if="($v.name.$dirty && !$v.name.minLength) || !$v.name.required" >&#10094;</span>
				<span class="done" v-if="$v.name.$dirty && $v.name.required && $v.name.minLength">&#10003;</span>
			</div>
<!--********** secondName **********-->
			<div class="item">
				<label for="secondName">–û—Ç—á–µ—Å—Ç–≤–æ:</label>
				<input type="text" id="secondName" name="secondName"
					pattern="[–ê-–Ø–∞-—è—ë–Å]{3,}"
					v-model.trim="secondName"
					@input="setSecondName($event.target.value)"
				/>
				<span class="error-title" v-if="$v.secondName.$dirty && !$v.secondName.minLength">–ü–æ–ª–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º {{$v.secondName.$params.minLength.min}} –±—É–∫–≤—ã</span>
				<span class="error" v-if="($v.secondName.$dirty && !$v.secondName.minLength)" >&#10094;</span>
				<span class="done" v-if="$v.secondName.$dirty && $v.secondName.required && $v.secondName.minLength">&#10003;</span>
			</div>
<!--********** birthday **********-->
			<div class="item item_birthday">
				<label for="birthday" class="required">–î–∞—Ç–∞ —Ä–æ–∂–¥–µ–Ω–∏—è:</label>
				<input type="date" id="birthday" name="birthday"
					required
					v-model.trim="birthday"
					@input="setBirthday($event.target.value)"
				/>
				<span class="error-title" v-if="$v.birthday.$dirty && !$v.birthday.required">–í—ã–±–µ—Ä–∏—Ç–µ –¥–∞—Ç—É —Å–≤–æ–µ–≥–æ —Ä–æ–∂–¥–µ–Ω–∏—è</span>
				<span class="error" v-if="($v.birthday.$dirty && !$v.birthday.required) || !$v.birthday.required" >&#10094;</span>
				<span class="done" v-if="$v.birthday.$dirty && $v.birthday.required">&#10003;</span>
			</div>
<!--********** phone **********-->
			<div class="item">
				<label for="phone" class="required">–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞:</label>
				<input type="phone" id="phone" name="phone" placeholder="–ù–∞—á–Ω–∏ —Å '7'"
					pattern="7[0-9]{10}"
					required
					v-model.trim="phone"
					@input="setPhone($event.target.value)"
				/>
				<span class="error-title" v-if="($v.phone.$dirty && !$v.phone.helpers ) || !$v.phone.numeric && $v.phone.required || !$v.phone.helpers">–í–≤–µ–¥–∏—Ç–µ 11 —Ü–∏—Ñ—Ä –Ω–∞—á–∏–Ω–∞—è —Å "7"</span>
				<span class="error" v-if="($v.phone.$dirty && !$v.phone.helpers ) || (!$v.phone.numeric && !$v.phone.required) || !$v.phone.helpers" >&#10094;</span>
				<span class="done" v-if="$v.phone.$dirty && $v.phone.required && $v.phone.helpers">&#10003;</span>
			</div>
<!--********** sex **********-->
			<div class="wrap">
				<span>–ü–æ–ª:</span>
				<div class="item item_sex">
					<input type="radio" id="female" name="sex" value="female"/>
					<label for="female">&#128105;</label>
				</div>
				<div class="item item_sex">
					<input type="radio" id="male" name="sex" value="male"/>
					<label for="male">&#128104;</label>
				</div>
			</div>
<!--********** client **********-->
			<div  class="item item_clients">
				<label class="required" for="client">–ì—Ä—É–ø–ø–∞ –∫–ª–∏–µ–Ω—Ç–æ–≤:</label>
				<select multiple title="–ù–µ—Å–∫–æ–ª—å–∫–æ: ctrl+click" id="client" name="client"
					required
					v-model.trim="client"
					@input="setClient($event.target.value)"
					@focus="clearArray()"
				>
					<option
						v-for="(category, ind) of clientCategory"
						v-bind:key="ind"
						v-bind:value="category"
					>
						{{ category }}
					</option>
				</select>
				<span class="error-title" v-if="$v.client.$dirty && !$v.client.required">–í—ã–±–µ—Ä–∏—Ç–µ –æ–¥–∏–Ω –∏–ª–∏ –Ω–µ—Å–∫–æ–ª—å–∫–æ –≤–∞—Ä–∏–∞–Ω—Ç–æ–≤</span>
				<span class="error" v-if="!$v.client.required" >&#10094;</span>
				<span class="done" v-if="$v.client.required" >&#10003;</span>
			</div>
<!--********** doctor **********-->
			<div class="item item_doctor">
				<label for="doctor">–õ–µ—á–∞—â–∏–π –≤—Ä–∞—á:</label>
				<select id="doctor" name="doctor"
					v-model.trim="doctor"
					@input="setDoctor($event.target.value)"
				>
					<option disabled selected>–í—ã–±–µ—Ä–∏—Ç–µ –≤—Ä–∞—á–∞</option>
					<option
						v-for="(doctor, ind) of doctors"
						v-bind:key="ind"
						v-bind:value="doctor"
					>
						{{ doctor }}
					</option>
				</select>
				<span class="done" v-if="$v.doctor.required" >&#10003;</span>
			</div>
		</div>
<!--********** sms **********-->
		<div class="item item_sms">
			<input type="checkbox" id="sms" name="sms"/>
			<label for="sms">–ù–µ –æ—Ç–ø—Ä–∞–≤–ª—è—Ç—å –°–ú–°</label>
		</div>
	</fieldset>
</template>

<script>
import { required, minLength, numeric, helpers } from 'vuelidate/lib/validators'

export default {
	data() {
		return {
			surname: '',
			name: '',
			secondName: '',
			birthday: '',
			phone: '',
			client: [],
			doctor: [],
			clientCategory: ['VIP', '–ü—Ä–æ–±–ª–µ–º–Ω—ã–µ', '–û–ú–°'],
			doctors: ['–ò–≤–∞–Ω–æ–≤', '–ó–∞—Ö–∞—Ä–æ–≤', '–ß–µ—Ä–Ω—ã—à–µ–≤–∞'],
		}
	},
	props: {

	},
	validations: {
		surname: {
			required,
			minLength: minLength(3)
		},
		name: {
			required,
			minLength: minLength(3)
		},
		secondName: {
			required,
			minLength: minLength(3)
		},
		birthday: {
			required,
		},
		phone: {
			required,
			numeric,
			helpers: helpers.regex('numeric', /^7[0-9]{10}$/)
		},
		client: {
			required
		},
		doctor: {
			required
		}
	},
	methods: {
		setSurname(value) {
			this.surname = value;
			this.$v.surname.$touch();
		},
		setName(value) {
			this.name = value;
			this.$v.name.$touch();
		},
		setSecondName(value) {
			this.secondName = value;
			this.$v.secondName.$touch();
		},
		setBirthday(value) {
			this.birthday = value;
			this.$v.birthday.$touch();
		},
		setPhone(value) {
			this.phone = value;
			this.$v.phone.$touch();
		},
		setClient(value) {
			if(!this.client.includes(value)) {
				this.client.push(value);
				this.$v.client.$touch();
			}
		},
		clearArray() {
			this.client = [];
		},
		setDoctor(value) {
			this.doctor = value;
			this.$v.doctor.$touch();
		},
	}
}
</script>

<style lang="sass" scoped>
.item
	&_birthday
		input
			height: 32px
			width: 202px
	&_sex
		display: flex
		flex-direction: row
		cursor: pointer
		margin: 0 30px 10px 0
		label
			cursor: pointer
		input
			cursor: pointer
			width: 20px
			margin: 0 10px 0 0
			padding: 0
	&_doctor
		select
			height: 36px
			width: 216px
	&_sms
		height: 30px
		flex-direction: row
		align-items: center
		text-decoration: underline
		label
			cursor: pointer
		input
			cursor: pointer
			width: 20px
			margin-right: 10px
			border: 2px solid red
.wrap
	height: 74px
	width: 220px
	display: flex
	flex-direction: row
	justify-content: space-between
	align-items: center
	margin: 0 30px 10px 0
	span
		display: block
		padding-bottom: 10px
		margin-right: 10px
</style>
